import { Component, h, Host, Prop } from '@stencil/core';

/**
 * @slot - Content placed inside of the trigger, after the 'label' prop if one is provided
 */
@Component({
    tag: 'atui-accordion-trigger',
})
export class AtuiAccordionTriggerComponent {
    /**
     * Text to display on the trigger for the accordion item.
     */
    @Prop() label: string;

    render() {
        return (
            <Host
                class="group flex flex-1 items-center justify-between p-8"
                data-name="accordion-trigger"
            >
                {this.label && (
                    <span class="flex flex-grow truncate text-sm font-medium group-hover:underline">
                        {this.label}
                    </span>
                )}
                <slot />
                <i
                    role="presentation"
                    aria-hidden="true"
                    class="material-icons text-md text-light group-data-[state=expanded]/accordion-item:rotate-180"
                >
                    keyboard_arrow_down
                </i>
            </Host>
        );
    }
}
